<template>
  <div>
    <NewsContent :fetchObject="fetchObject"></NewsContent>
  </div>
</template>
<script>
  import NewsContent from '@/components/NewsContent.vue'
  import * as utils from '@/common/utils.js'

  export default {
    name: 'newsDetail',
    data() {
      return {
        fetchObject: {
          url: this.apiConfig.newsDetail,
          method: 'GET',
          params: {}
        }
      }
    },
    components: {
      NewsContent
    },
    created() {
      var id = utils.getQueryString(window.location.href, 'id')
      setTimeout(() => {
        this.$store.dispatch('UPDATE_LOADINGSTATE', false)
      }, 1000)
      if (id !== null) {
        this.fetchObject.params = {id}
      } else {
        setTimeout(() => {
          this.$router.push({
            name: 'Index'
          })
        }, 1000)
      }
    }
  }
</script>
