<template>
  <div>
    <div class="pl15 pr15 pt10 px-b bgc-white">
      <h3 class="f17">{{ fetchData.information_title || fetchData.title }}</h3>
      <div class="box-justify gray pb10">
        <div class="elips text-overflow" v-if="fetchData.type_name || fetchData.typeName">{{ fetchData.type_name ||
          fetchData.typeName}}
        </div>
        <div>{{ fetchData.created_at || fetchData.publish }}</div>
        <div v-if="fetchData.information_number || fetchData.click">
          <img src="../assets/images/view.png" width="20" alt="">
          <span class="ml5">{{ fetchData.information_number || fetchData.click }}</span>
        </div>
      </div>
    </div>
    <div v-if="fetchData.information_content || fetchData.content">
      <p class="p15 dark f16 bgc-white information_content"
         v-html="fetchData.information_content || fetchData.content"></p>
    </div>
  </div>
</template>

<script>
  import {wxShare} from "../common/wxShare"
  export default {
    name: 'newsContent',
    props: {
      fetchObject: Object
    },
    data() {
      return {
        fetchData: {}
      }
    },
    created() {
      let {method, url, data, params} = this.fetchObject
      let fetchObject
      if (method.toLocaleUpperCase() === 'GET') {
        fetchObject = {method, url, params}
      } else {
        fetchObject = {method, url, data}
      }
      this.axios(fetchObject).then(res => {
        if (res.s === 0) {
          this.$toast.fail(res.m)
          setTimeout(() => {
            this.$router.push({
              name: 'Index'
            })
          }, 1300)
          return
        }
        this.fetchData = res.d
        let title = this.fetchData.information_title || this.fetchData.title;
        let desc = '不容错过的资讯内容'
        wxShare({
          title,
          desc,
          link: window.location.href,
          imgUrl: res.d.image_url,
          success: function () {
            // alert('分享成功1')
          }
        })
        document.title = this.fetchData.information_title || this.fetchData.title
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .elips {
    max-width: 35vw;
  }
  .information_content {
    line-height: 26px;

  img {
    margin: rem(4px) auto;
  }
  }
</style>
